'use client';

import { useState } from 'react';
import Image from 'next/image';
import { PaperAirplaneIcon } from '@heroicons/react/24/solid';
import Navbar from '@/components/Navbar';
import Footer from '@/components/Footer';

// بيانات وهمية للمحادثات
const conversations = [
  {
    id: 1,
    name: 'محمد علي',
    avatar: '/images/users/user1.jpg',
    lastMessage: 'شكراً لك على التصميم الرائع!',
    time: 'منذ 5 دقائق',
    unread: 0,
    status: 'online',
  },
  {
    id: 2,
    name: 'سارة أحمد',
    avatar: '/images/users/user2.jpg',
    lastMessage: 'هل يمكنك إجراء بعض التعديلات على الفيديو؟',
    time: 'منذ 30 دقيقة',
    unread: 2,
    status: 'offline',
  },
  {
    id: 3,
    name: 'أحمد محمود',
    avatar: '/images/users/user3.jpg',
    lastMessage: 'أريد طلب خدمة جديدة',
    time: 'منذ 2 ساعة',
    unread: 1,
    status: 'online',
  },
  {
    id: 4,
    name: 'فاطمة حسن',
    avatar: '/images/users/user4.jpg',
    lastMessage: 'تم الانتهاء من المشروع',
    time: 'أمس',
    unread: 0,
    status: 'offline',
  },
];

// بيانات وهمية للرسائل
const messages = [
  {
    id: 1,
    sender: 'أنا',
    content: 'مرحباً، كيف يمكنني مساعدتك في مشروع توليد الصور؟',
    time: '10:30 صباحاً',
    isMe: true,
  },
  {
    id: 2,
    sender: 'محمد علي',
    content: 'مرحباً! أحتاج إلى تصميم 5 صور بأسلوب فني معين لموقعي التجاري الجديد',
    time: '10:32 صباحاً',
    isMe: false,
  },
  {
    id: 3,
    sender: 'أنا',
    content: 'بالتأكيد، هل لديك أي أمثلة أو مرجعيات للأسلوب الذي تفضله؟',
    time: '10:35 صباحاً',
    isMe: true,
  },
  {
    id: 4,
    sender: 'محمد علي',
    content: 'نعم، سأرسل لك بعض الأمثلة التي أعجبتني. أريد أسلوباً مشابهاً مع التركيز على الألوان الزرقاء والذهبية لتتناسب مع هوية العلامة التجارية.',
    time: '10:40 صباحاً',
    isMe: false,
  },
  {
    id: 5,
    sender: 'أنا',
    content: 'ممتاز! سأبدأ العمل على التصاميم بمجرد استلام الأمثلة. هل لديك موعد نهائي محدد لإنجاز المشروع؟',
    time: '10:42 صباحاً',
    isMe: true,
  },
  {
    id: 6,
    sender: 'محمد علي',
    content: 'أتمنى الانتهاء منها خلال أسبوع إن أمكن، فنحن نخطط لإطلاق الموقع الجديد قريباً',
    time: '10:45 صباحاً',
    isMe: false,
  },
  {
    id: 7,
    sender: 'محمد علي',
    content: 'وبالمناسبة، شكراً لك على الاستجابة السريعة!',
    time: '10:46 صباحاً',
    isMe: false,
  },
];

export default function MessagesPage() {
  const [selectedConversation, setSelectedConversation] = useState(1);
  const [messageText, setMessageText] = useState('');

  const handleSendMessage = (e: React.FormEvent) => {
    e.preventDefault();
    if (messageText.trim()) {
      // في تطبيق حقيقي، هنا سيتم إرسال الرسالة إلى API
      console.log('Sending message:', messageText);
      setMessageText('');
    }
  };

  return (
    <>
      <Navbar />
      <div className="bg-gray-50 py-6">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="bg-white rounded-lg shadow overflow-hidden">
            <div className="grid grid-cols-3 h-[calc(100vh-200px)]">
              {/* قائمة المحادثات */}
              <div className="col-span-1 border-l border-gray-200 overflow-y-auto">
                <div className="px-4 py-3 border-b border-gray-200">
                  <h2 className="text-lg font-medium text-gray-900">المحادثات</h2>
                </div>
                <ul className="divide-y divide-gray-200">
                  {conversations.map((conversation) => (
                    <li
                      key={conversation.id}
                      className={`cursor-pointer hover:bg-gray-50 ${
                        selectedConversation === conversation.id ? 'bg-indigo-50' : ''
                      }`}
                      onClick={() => setSelectedConversation(conversation.id)}
                    >
                      <div className="px-4 py-4 flex items-center">
                        <div className="relative">
                          <div className="h-10 w-10 rounded-full bg-gray-200 overflow-hidden">
                            <Image
                              src={conversation.avatar}
                              alt={conversation.name}
                              width={40}
                              height={40}
                              className="h-full w-full object-cover"
                            />
                          </div>
                          {conversation.status === 'online' && (
                            <span className="absolute bottom-0 left-0 h-2.5 w-2.5 rounded-full bg-green-400 ring-1 ring-white"></span>
                          )}
                        </div>
                        <div className="mr-3 flex-1 min-w-0">
                          <div className="flex items-center justify-between">
                            <p className="text-sm font-medium text-gray-900 truncate">{conversation.name}</p>
                            <p className="text-xs text-gray-500">{conversation.time}</p>
                          </div>
                          <p className="text-sm text-gray-500 truncate">{conversation.lastMessage}</p>
                        </div>
                        {conversation.unread > 0 && (
                          <div className="mr-2 flex-shrink-0 h-5 w-5 rounded-full bg-indigo-600 flex items-center justify-center">
                            <span className="text-xs font-medium text-white">{conversation.unread}</span>
                          </div>
                        )}
                      </div>
                    </li>
                  ))}
                </ul>
              </div>
              
              {/* منطقة الرسائل */}
              <div className="col-span-2 flex flex-col">
                <div className="px-4 py-3 border-b border-gray-200 flex items-center">
                  <div className="relative">
                    <div className="h-10 w-10 rounded-full bg-gray-200 overflow-hidden">
                      <Image
                        src={conversations.find(c => c.id === selectedConversation)?.avatar || ''}
                        alt={conversations.find(c => c.id === selectedConversation)?.name || ''}
                        width={40}
                        height={40}
                        className="h-full w-full object-cover"
                      />
                    </div>
                    {conversations.find(c => c.id === selectedConversation)?.status === 'online' && (
                      <span className="absolute bottom-0 left-0 h-2.5 w-2.5 rounded-full bg-green-400 ring-1 ring-white"></span>
                    )}
                  </div>
                  <div className="mr-3">
                    <h2 className="text-lg font-medium text-gray-900">
                      {conversations.find(c => c.id === selectedConversation)?.name}
                    </h2>
                    <p className="text-sm text-gray-500">
                      {conversations.find(c => c.id === selectedConversation)?.status === 'online' 
                        ? 'متصل الآن' 
                        : 'غير متصل'}
                    </p>
                  </div>
                </div>
                
                <div className="flex-1 overflow-y-auto px-4 py-4 space-y-4">
                  {messages.map((message) => (
                    <div
                      key={message.id}
                      className={`flex ${message.isMe ? 'justify-end' : 'justify-start'}`}
                    >
                      <div
                        className={`rounded-lg px-4 py-2 max-w-xs sm:max-w-md ${
                          message.isMe
                            ? 'bg-indigo-100 text-indigo-900'
                            : 'bg-gray-100 text-gray-900'
                        }`}
                      >
                        <p className="text-sm">{message.content}</p>
                        <span className="block text-xs text-gray-500 mt-1 text-left">
                          {message.time}
                        </span>
                      </div>
                    </div>
                  ))}
                </div>
                
                <div className="border-t border-gray-200 p-4">
                  <form onSubmit={handleSendMessage} className="flex items-center">
                    <input
                      type="text"
                      value={messageText}
                      onChange={(e) => setMessageText(e.target.value)}
                      placeholder="اكتب رسالتك هنا..."
                      className="flex-1 rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500"
                    />
                    <button
                      type="submit"
                      className="mr-2 inline-flex items-center justify-center rounded-md border border-transparent bg-indigo-600 p-2 text-white shadow-sm hover:bg-indigo-700 focus:outline-none"
                    >
                      <PaperAirplaneIcon className="h-5 w-5" />
                    </button>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <Footer />
    </>
  );
} 